const i18n = require('$i18n');

i18n.init({
  language:'en-US'
});

console.log(i18n('hello'));
console.log(i18n('hello', { name: 'i18n' }));
console.log(i18n('test1', null, 'test1-default1'));
console.log(i18n('test2'));
console.log(i18n('test1', 'test1-default2'));



